.Card {
  padding-top: 12px;
  padding-bottom: 18px;
  border-top: 1px solid var(--outline-color);
}

.CardHeading {
  display: flex;
  align-items: center;
}

.SourceIcon {
  width: 32px;
  height: 32px;
  margin-right: 12px;
  border-radius: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--outline-color);
}

.SourcePubDate {
  color: var(--light-text-color);
}
