:root {
  --primary-color: rgb(122, 122, 255);
  --primary-dark-color: rgb(100, 100, 200);
  --outline-color: rgb(232, 235, 238);
  --translucent-background: rgba(255, 255, 255, 0.9);
  --light-text-color: rgb(120, 122, 124);
}

h1 {
  font-size: 3em;
}

button {
  width: 80%;
  font-size: inherit;
  color: white;
  background: var(--primary-color);
  padding: 12px;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  user-select: none;
  -webkit-user-select: none;
  cursor: pointer;
}

button:hover {
  background: var(--primary-dark-color);
}

