.TabView {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.BottomAppBar {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  background: var(--translucent-background);
  border-top: 1px solid var(--outline-color);
  padding-bottom: env(safe-area-inset-bottom);
}

.BottomAppBar div {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  min-height: 48px;
  min-width: 48px;
}
