.LoadIndicator {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.LoadIndicatorIcon {
  width: 32px;
  height: 32px;
  border: 5px solid rgb(235, 235, 235, 0.8);
  border-top: 5px solid var(--primary-dark-color);
  border-radius: 100%;
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
