.ChannelModalOuter {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.5);
  z-index: 5;
}

.ChannelModal {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 22px;
  border-radius: 6px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 4px 0 rgba(0, 0, 0, 0.32);
}

.ChannelModal input, .ChannelModal button {
  font-size: 16px;
  padding: 6px 8px;
  width: 100%;
  box-sizing: border-box;
  min-width: 300px;
  margin-top: 8px;
}

.FabAddButton {
  display: flex;
  padding: 8px;
  position: fixed;
  right: 22px;
  bottom: calc(84px + env(safe-area-inset-bottom));
  background: var(--primary-color);
  border-radius: 100%;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 4px 0 rgba(0, 0, 0, 0.32);
  cursor: pointer;
  z-index: 1;
}
